@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  border: 0;
}

.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  text-align: center;
  background: rgb(62, 157, 106);
  background: linear-gradient(
    90deg,
    rgba(62, 157, 106, 1) 0%,
    rgba(165, 233, 222, 1) 100%,
    rgba(212, 212, 224, 0.6895133053221288) 100%
  );
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.class-item {
  width: auto;
  max-width: 249.9px;
  margin: 20px 5px 10px 10px;
  background-color: gray;
  border-radius: 10%;
}

.class-item__title {
  height: auto;
  min-height: 50px;
  max-width: inherit;
  display: grid;
  grid-template-columns: 12px repeat(7, 1fr);
  justify-content: center;
  align-content: center;
}

.class-item__title img {
  width: 100%;
  height: 100%;
  grid-column: 2/3;
  border-radius: 40%;
}

.class-item__title h1 {
  font-size: 25px;
  grid-column: 3/8;
}

.class-item__image {
  width: auto;
  background: rgb(212, 212, 224);
  background: linear-gradient(
    90deg,
    rgba(212, 212, 224, 0.6895133053221288) 0%,
    rgba(127, 131, 131, 1) 100%,
    rgba(2, 0, 36, 1) 100%
  );
}

.class-item__details {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(1, 1fr);
}

.class-item__details ul {
  padding: 0;
  grid-column: 3/7;
  justify-content: left;
  align-content: center;
  font-size: medium;
  font-family: "Roboto Slab", serif;
  font-weight: 600;
  list-style-type: none;
}

