.class-item__button__male {
  grid-column: 1/3;
  border-radius: 0 0 0 44%;
  background-image: url("https://img.icons8.com/nolan/64/male.png");
  background-color: gray;
  border-style: outset;
  border: solid;
}

.class-item__button__female {
  grid-column: 7/9;
  border-radius: 0 0 44% 0;
  background-image: url("https://img.icons8.com/nolan/64/female.png");
  background-color: gray;
  border-style: outset;
  border: solid;
}

